          box-shadow: 0 1px 5px rgba(0, 0, 0, 0.15);
}
.org-tree-node-label-stuck-neck-left {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: absolute;
  display: inline-block;
  margin-left: 1px;
}
.org-tree-node-label-stuck-neck-left:before {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  content: "";
  position: absolute;
  top: var(--beforTop);
  left: 100%;
  width: 50%;
  height: var(--afterBeforHeight);
  border-top: var(--lineWidthHorizontal) solid var(--lineCorlor);
  border-right: var(--lineWidthVertical) solid var(--lineCorlor);
}
.org-tree-node-label-stuck-neck-left::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  content: "";
  position: absolute;
  top: var(--afterTop);
  left: 100%;
  width: 50%;
  height: var(--afterBeforHeight);
  border-right: var(--lineWidthVertical) solid var(--lineCorlor);
}
.org-tree-node-label-stuck-neck-left .org-tree-node-label-inner {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 10px 15px;
  text-align: center;
  -webkit-box-shadow: 0 1px 5px rgba(0, 0, 0, 0.15);
          box-shadow: 0 1px 5px rgba(0, 0, 0, 0.15);
}
.org-tree-node-label-stuck-neck-right {
  position: absolute;
  display: inline-block;
  margin-left: 1px;
}
.org-tree-node-label-stuck-neck-right:before {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  content: "";
  position: absolute;
  top: var(--beforTop);
  right: 100%;
  width: 50%;
  height: var(--afterBeforHeight);
  border-top: var(--lineWidthHorizontal) solid var(--lineCorlor);
  border-left: var(--lineWidthVertical) solid var(--lineCorlor);
}
.org-tree-node-label-stuck-neck-right::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  content: "";
  position: absolute;
  top: var(--afterTop);
  right: 100%;
  width: 50%;
  height: var(--afterBeforHeight);
