          box-shadow: 0 1px 5px rgba(0, 0, 0, 0.15);
}
.org-tree-node-btn {
  position: absolute;
  top: 112%;
  left: 50%;
  width: 20px;
  height: 20px;
  z-index: 10;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 50%;
  margin-left: -11px;
  -webkit-box-shadow: 0 0 2px rgba(0, 0, 0, 0.15);
          box-shadow: 0 0 2px rgba(0, 0, 0, 0.15);
  cursor: pointer;
  -webkit-transition: all 0.35s ease;
  transition: all 0.35s ease;
}
.org-tree-node-btn:hover {
  background-color: #e7e8e9;
  -webkit-transform: scale(1.15);
          transform: scale(1.15);
}
.org-tree-node-btn:before,
.org-tree-node-btn:after {
  content: "";
  position: absolute;
}
.org-tree-node-btn:before {
  top: 50%;
  left: 4px;
  right: 4px;
  height: 0;
  border-top: 1px solid #ccc;
}
.org-tree-node-btn:after {
  top: 4px;
  left: 50%;
  bottom: 4px;
  width: 0;
  border-left: 1px solid #ccc;
}
.org-tree-node-btn.expanded:after {
  border: none;
}
.org-tree-node-btn-stuck-neck {
  top: 78px;
  display: none;
}
.org-tree-node {
  padding: 20px;
  display: table-cell;
  vertical-align: top;
}
.org-tree-node.is-leaf,
.org-tree-node.collapsed {
  padding-left: 10px;
  padding-right: 10px;
}
.org-tree-node:before,
.org-tree-node:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 19px;
}
.org-tree-node:after {
  left: 50%;
  border-left: var(--lineWidthVertical) solid var(--lineCorlor);
}
.org-tree-node:not(:first-child):before,
.org-tree-node:not(:last-child):after {
  border-top: var(--lineWidthHorizontal) solid var(--lineCorlor);
}
.org-tree-node-stuck-neck {
