          box-sizing: border-box;
}
.collapsable .org-tree-node.collapsed .org-tree-node-label:after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  content: "";
  position: absolute;
  top: 100%;
  left: 0;
  width: 50%;
  height: 20px;
  border-right: var(--lineWidthVertical) solid var(--lineCorlor);
}
.org-tree > .org-tree-node {
  padding-top: 19px;
}
.org-tree > .org-tree-node:after {
  border-left: 0;
}
.org-tree-node-children {
  padding-top: 20px;
  display: table;
}
.org-tree-node-children:before {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  width: 0;
  height: var(--afterHeight);
  border-left: 2px solid var(--lineCorlor);
}
.org-tree-node-children:after {
  content: "";
  display: table;
  clear: both;
}
.org-tree-node-children-stuck-neck {
  padding-top: 60px;
  display: table;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
